body {
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  box-sizing: border-box;
  width: 200px;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
}

.card__pic {
  width: 160px;
  height: 134px;
  margin: auto;
  display: block;
}

.card__name {
  text-align: left;
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-top: -8px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  align-self: end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-top: 12px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__price--label {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price--value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding: 10px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  transition: all .3s;
}
